
* {
    font-family: Arial, Helvetica, sans-serif;
}

@media screen and (min-width:390px) and (max-width:750px){
    #calender {
        height: 50vh;
        // max-width: fit-content;
        // border: solid purple;
        // height: 59%;
        background-color: var(--background-color);
    }
    
    #header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        // margin: 1rem;
        // background-color: white;
        padding: 1.5rem 0;
        color: #272C38;
        background-color: var(--background-color);
        
        #header_left {
            // border: solid ;
            width: 100%;
            display: flex;
        }
        
        .arrows {
            // border: solid orange;
            width: 25%;
            display: flex;
        }
        
        .dates {
            // border: solid green;   
            width: 95%;
            display: flex;
            align-items: center;
        }
        
        span {
            // border: solid hsl(16, 100%, 90%) 1px;
            border-radius: 100%;
            width: 30px;
            height: 30px;
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            margin: 0 0 0 5px;
            
            &:active {
                background-color: hsl(16, 100%, 70%);
                color: #ddd;
            }
        }
        
        h3 {
            margin: 0 10px 0 0;
            // border: solid red;
        }
        
        p {
            font-size: .9rem;
            margin-left: .5rem;
            margin-top: 1px;
            color: orangered;
        }
        
        .inactive_burger {
            // border: solid red;
            margin-right: 0.4rem;
            display: flex;
            flex-direction: column;
            justify-content: center;
            height: 30px;
            align-items: center;
            cursor: pointer;
            
            :nth-child(1) {
                transform: rotate(180deg);
                transition: 0.3s ease-in-out;
            }
            
            :nth-child(2){
                width: 20px;
                align-self: flex-end;
                transition: 0.3s ease-in-out;
                opacity: 1;
            }
            
            :nth-child(3) {
                transform: rotate(-360deg);
                transition: 0.3s ease-in-out;
                height: 3px;
            }
            
            
            span {
                border: solid #eee 1px;
                margin: 3px;
                border-radius: 20px;
                height: 1px;
                width: 25px;
                position: relative;
            }
        }
        
        .active_burger {
            // border: solid red;
            margin-right: 0.4rem;
            display: flex;
            flex-direction: column;
            justify-content: center;
            height: 30px;
            align-items: center;
            cursor: pointer;
            
            :nth-child(1) {
                transform: rotate(45deg) translate(5px,5px);
                transition: 0.4s ease-in-out;
            }
            
            
            :nth-child(2){
                transform: translateX(-20px);
                width: 20px;
                align-self: flex-end;
                transition: 0.4s ease-in-out;
                opacity: 0;
            }
            
            :nth-child(3) {
                transform: rotate(-45deg) translate(7px,-6px);
                transition: 0.4s ease-in-out;
            }
            
            span {
                border: solid gray 1px;
                margin: 3px;;
                border-radius: 20px;
                height: 1px;
                width: 25px;
                position: relative;
            }
        }
    }
    
    .weekdays {
        // display: flex;
        // justify-content: space-around;
        display: grid;
        grid-template-columns: repeat(7,1fr);
        justify-items: center;
        margin: .5rem auto;
        padding-left: 1rem;
        // border: solid gray 1px;
        // background-color: #272C38;
        // background-color: #ddd;
        color: #272C38;
        background-color: var(--background-color);
        
        span {
            font-size: .9rem;
            display: flex;
            justify-content: center;
            align-items: center;
            // border: solid red;
            width: 40px;
            padding: 0.5em;
            margin: 10px 6px;
        }
    }
    
    main {
        // background: hsl(0, 0%, 97%);
        // background-color: #272C38;
        // background-color: #ddd;
        background-color: var(--background-color);
    }
    
    .days {
        display: grid;
        grid-template-columns: repeat(7,1fr);
        justify-items: center;
        // flex-wrap: wrap;
        // justify-content: space-around;
        // background-color: hsl(0, 0%, 100%);
        // background-color: #272C38;
        // height: 30px;
        // border: solid gray 1px;
        padding-left: 1rem;
        margin: .5rem auto;
        background-color: var(--background-color);
        
        .day_cell {
            display: flex;
            justify-content: center;
            border-radius: 10px;
            align-items: center;
            // background-color: white;
            width: 40px;
            padding: 0.5em;
            margin: 10px 6px;
            // border: solid lightgray 1px;
            cursor: pointer;
            color: hsl(189, 43%, 36%);
            
        }
        
        
        .selected_cell {
            display: flex;
            justify-content: center;
            border-radius: 10px;
            align-items: center;
            // background-image: linear-gradient(to bottom right,#EEBD89,#D13ABD);
            background-color: hsl(15, 90%, 40%);
            color: white;
            width: 40px;
            border: none;
            padding: 0.5em;
            margin: 10px 6px;
            cursor: pointer;
            
            &:active {
                background: orangered;
            }
        }
        
    }
    .padding_day {
        // border: solid red;
        // background-color: gray;
        color: #888;
    }
    
}